import { useState, useEffect } from "react";

const MOBILE_BREAKPOINT = 768; // Consistente com MapaBase.js

// Verificação segura de APIs do navegador
const isBrowser = typeof window !== "undefined" && typeof document !== "undefined";

const usePainelVisibility = (painelInfo) => {
  console.group("usePainelVisibility - Hook");
  console.log("Input:", {
    painelInfo: {
      titulo: painelInfo?.titulo,
      tipo: painelInfo?.tipo,
      painelInfoType: typeof painelInfo,
      isNull: painelInfo === null,
      isUndefined: painelInfo === undefined
    }
  });

  const [isVisible, setIsVisible] = useState(false);
  const [isMobile, setIsMobile] = useState(isBrowser ? window.innerWidth <= MOBILE_BREAKPOINT : false);
  const [isMaximized, setIsMaximized] = useState(false);

  if (process.env.NODE_ENV !== 'production') {
    console.log("usePainelVisibility - painelInfo:", painelInfo);
  }

  // Logs de depuração em produção
  useEffect(() => {
    console.group("usePainelVisibility - Debug Info");
    console.log("painelInfo:", painelInfo);
    console.log("isBrowser:", isBrowser);
    console.log("window width:", isBrowser ? window.innerWidth : "N/A");
    console.log("isMobile:", isMobile);
    console.log("isVisible:", isVisible);
    console.groupEnd();
  }, [painelInfo, isMobile, isVisible]);

  // Gerenciamento de redimensionamento seguro
  useEffect(() => {
    if (!isBrowser) return;

    const checkMobile = () => {
      const width = window.innerWidth;
      console.log("Window resized:", width);
      setIsMobile(width <= MOBILE_BREAKPOINT);
    };

    window.addEventListener("resize", checkMobile);
    return () => window.removeEventListener("resize", checkMobile);
  }, []);

  // Gerenciamento de visibilidade seguro
  useEffect(() => {
    console.log("usePainelVisibility - Efeito de visibilidade");
    console.log("Estado atual:", {
      isVisible,
      isMobile,
      isMaximized,
      hasPainelInfo: !!painelInfo
    });

    if (painelInfo) {
      console.log("Tornando painel visível");
      setIsVisible(true);
      
      // Limpa o overflow do body
      if (typeof document !== 'undefined') {
        document.body.style.overflow = 'hidden';
      }
    } else {
      console.log("Tornando painel invisível");
      setIsVisible(false);
      
      // Restaura o overflow do body
      if (typeof document !== 'undefined') {
        document.body.style.overflow = '';
      }
    }

    return () => {
      console.log("usePainelVisibility - Cleanup");
      if (typeof document !== 'undefined') {
        document.body.style.overflow = '';
      }
    };
  }, [painelInfo]);

  if (process.env.NODE_ENV !== 'production') {
    console.log("usePainelVisibility - returning state:", { isVisible, isMobile });
  }

  console.log("usePainelVisibility - Retorno:", {
    isVisible,
    isMobile,
    isMaximized
  });
  console.groupEnd();

  return { 
    isVisible, 
    isMobile,
    isMaximized,
    debug: {
      isBrowser,
      windowWidth: isBrowser ? window.innerWidth : null,
      painelInfoType: typeof painelInfo,
      hasPainelInfo: !!painelInfo
    }
  };
};

export default usePainelVisibility;